@import url(https://fonts.googleapis.com/css?family=Orbitron);
body {
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: 'Orbitron', sans-serif;
  background: linear-gradient(to bottom, #b97979 0%, #864646 100%);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed; }

#wrapper {
  height: 100vh;
  width: 100vw; }

#calculator {
  background-color: #b1bdc0;
  background-image: url("http://getwallpapers.com/wallpaper/full/7/d/2/159283.jpg");
  border-radius: 15px;
  border-bottom: #5a5a5a solid 1px;
  height: 525px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 0 3px 0px 0px #afafaf, 0 6px 0px 0px #a0a0a0, 0 9px 0px 0px #8c8c8c, 0 12px 0px 0px #828282, 0 14px 0px 0px #787878; }
  #calculator::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    height: 540px;
    border-radius: 15px;
    border-bottom: #000000 1px solid; }
  #calculator #screen {
    color: #5252a7;
    font-size: 1.6rem;
    letter-spacing: 2px;
    background-color: #b1bdc0;
    border-top: #cdcbcb solid 1px;
    border-bottom: #9b9797 solid 1px; }
    #calculator #screen .message {
      color: #e01212;
      font-size: 1rem; }
  #calculator #title, #calculator #subtitle {
    text-shadow: 0 -2px 0 black, 0 1px 1px #999999;
    color: #393939;
    letter-spacing: 2px;
    font-size: 1.6rem; }
  #calculator #subtitle {
    font-size: .9rem; }

.key {
  cursor: pointer;
  font-size: 1.6em;
  font-weight: bold;
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
  transition: all .2s; }
  .key:active {
    transform: translate(0px, 3px);
    border-bottom: 1px solid;
    box-shadow: none; }

.number {
  text-shadow: 0 -2px 0 black, 0 1px 1px #3edef1;
  color: #07535c;
  border: #0A7E8C solid 1px;
  background: linear-gradient(to bottom, #0fbed3 25%, #0A7E8C 100%);
  border-top: 1px solid #0c98a9;
  border-bottom: 1px solid #0da9bc;
  box-shadow: 0 4px 0 #07535c, 0 5px 10px rgba(0, 0, 0, 0.25); }
  .number:hover {
    background: linear-gradient(to bottom, #26daf0 30%, #0da9bc 80%); }

.operator {
  text-shadow: 0 -2px 0 #03282d, 0 1px 1px #9deef8;
  color: #0c93a4;
  border: #6de6f5 solid 1px;
  background: linear-gradient(to bottom, #6de6f5 25%, #6de6f5 100%);
  box-shadow: 0 4px 0 #0a7e8c, 0 5px 10px rgba(0, 0, 0, 0.25); }
  .operator:hover {
    background: linear-gradient(to bottom, #26daf0 30%, #56e2f3 80%); }

.action {
  text-shadow: 0 -2px 0 #541c23, 0 1px 1px #e8bac0;
  color: #a13543;
  border: #c24757 solid 1px;
  background: linear-gradient(to bottom, #d5808b 25%, #c24757 100%);
  border-top: 1px solid #ca5e6c;
  border-bottom: 1px solid #cf6d7a;
  box-shadow: 0 4px 0 #a13543, 0 5px 10px rgba(0, 0, 0, 0.25); }
  .action:hover {
    background: linear-gradient(to bottom, #e2a7ae 30%, #cf6d7a 80%); }

#equals {
  text-shadow: 0 -2px 0 #09240f, 0 1px 1px #80df95;
  color: #1e7531;
  border: #289e42 solid 1px;
  background: linear-gradient(to bottom, #43cf62 25%, #289e42 100%);
  border-top: 1px solid #2eb64c;
  border-bottom: 1px solid #32c753;
  box-shadow: 0 4px 0 #1e7531, 0 5px 10px rgba(0, 0, 0, 0.25); }
  #equals:hover {
    background: linear-gradient(to bottom, #6cda84 30%, #32c753 80%); }

#multiply {
  font-size: 1.1em; }

#del {
  font-size: 2.5em; }

